.ziv-law--wrapper{
    display: grid;
    height: 100vh;
    width: 100vw;
    grid-template-columns: 1fr;
    grid-template-rows:  1fr 1fr;
    text-align: center;
}
.ziv-law--title{
    font-size: 40px;
    box-sizing: border-box;
    max-width: 600px;
    margin: 0 auto;
    padding-top: 12vw;
}
.ziv-law--image{
    height: 200px;
    border-radius: 50%;
    border: 8px solid grey;
}
.ziv-law--counter{
    font-size: 80px;
}